import React from 'react'
import { Container, Box, useMediaQuery } from '@mui/material'
import { createTheme, ThemeProvider } from '@mui/material/styles';

export function Hero() {
    const query400px = useMediaQuery('(min-width:400px)');
    let cards_style = {
        minWidth: 250,
        maxWidth: 350.4,
    }
    if (!query400px) {
        cards_style['marginTop'] = '10px'
        cards_style['marginBottom'] = '10px'
    }
    else {
        cards_style['height'] = '100%'
    }

    return (
        <Box sx={{
            backgroundAttachment: 'fixed',
            textAlign: 'center',
            width: '100%',
            height: '90vh',
            background: 'top center no-repeat',
            backgroundImage: 'linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)), url("./assets/img/hero-background.jpg")',
            backgroundSize: 'cover',
            display: 'flex',
            justifyContent: 'center',
            flexDirection: 'column',
        }}>
            <Container>
                <div id="hero-title-container">
                    <h1>Cartera creativa New Code</h1>
                    <p>En New Code ofrecemos una amplia variedad de servicios para satisfacer las
                        necesidades de nuestros clientes. Nuestra cartera de servicios incluye:</p>
                </div>
            </Container>

        </Box>
    )
}
