import React, { useEffect } from 'react';
import { Hero } from './components/Hero'
import { ResponsiveAppBar } from './components/Navbar'
import { MarketingSection } from './components/MarketingSection'
import { DesignSection } from './components/DesignSection'
import { VideoAndPhotoSection } from './components/VideoAndPhotoSection';
import './assets/css/styles.css'
import { WebSection } from './components/WebSection';
import { ClientsSection } from './components/ClientsSection';
import AOS from 'aos';
import 'aos/dist/aos.css';


export function App() {
    const BrandName = 'New Code'
    useEffect(() => {
        AOS.init();
    }, [])

    return (
        <>
            <div id="Inicio"></div>
            <ResponsiveAppBar brand_name={BrandName} pages={['Inicio', 'Marketing Digital', 'Diseo Grfico', 'Video y Fotografa', 'Desarrollo Web']} />
            <Hero />
            {/* <div id="Marketing Digital" data-aos="fade-down" data-aos-duration="1000" data-aos-delay="300"> */}
            <div id="Marketing Digital">
                <MarketingSection />
            </div>
            <div id="Diseo Grfico">
            {/* <div id="Diseo Grfico" data-aos="fade-down" data-aos-duration="1000" data-aos-delay="300"> */}
                <DesignSection />
            </div>
            <div id="Video y Fotografa">
            {/* <div id="Video y Fotografa" data-aos="fade-down" data-aos-duration="1000" data-aos-delay="300"> */}
                <VideoAndPhotoSection />
            </div>
            {/* <div id="Desarrollo Web" data-aos="fade-down" data-aos-duration="1000" data-aos-delay="300"> */}
            <div id="Desarrollo Web">
                <WebSection />
            </div>
            <ClientsSection />
        </>
    )
}